@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Inter-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Inter-Medium.woff2") format("woff2");
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2");
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Inter-Bold.woff2") format("woff2");
}
html {
  scroll-behavior: smooth;
}
.modal-container {
  position: fixed;
  display: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(15, 17, 74, 0.8);
  z-index: 10;
}
.modal-container.modal-opened {
  display: block;
}
.modal-container--nojs {
  position: static;
  display: block;
  height: 100vh;
  background-color: rgba(15, 17, 74, 0.6);
}
.page-container {
  width: 320px;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
  box-sizing: border-box;
}
@media (min-width: 640px) {
  .page-container {
    width: 640px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (min-width: 1920px) {
  .page-container {
    width: 1920px;
    padding-left: 240px;
    padding-right: 240px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 640px) {
  .advantages {
    padding-bottom: 20px;
  }
}
@media (min-width: 1920px) {
  .advantages {
    padding-bottom: 114px;
  }
}
@media (min-width: 1920px) {
  .advantages__content-wrapper {
    display: flex;
    align-items: center;
    margin-top: 68px;
  }
}
.advantages__results {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 179px;
  margin-bottom: 22px;
  margin-top: 26px;
  padding-left: 14px;
  background-image: linear-gradient(270deg, rgba(255, 255, 255, 0) 0%, #ffffff 100%), url("../img/living-room_mobile@1x.webp");
  background-size: 100%, cover;
  border-radius: 16px;
}
@media (min-resolution: 2dppx) {
