.advantages__item {
  position: relative;
  margin-bottom: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: 0.25px;
  opacity: 0.9;
}
.advantages__item:last-child {
  padding-top: 3px;
  padding-bottom: 3px;
}
.advantages__item::before {
  position: absolute;
  content: "";
  width: 40px;
  height: 40px;
  left: -48px;
  background-image: url("../img/icons/advantages-marker.svg");
  background-size: 40px 40px;
  background-position: 0 0;
  background-repeat: no-repeat;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 50%;
}
.advantages__item:nth-child(2)::before {
  transform: translateY(-50%) rotate(45deg);
}
.advantages__item:nth-child(3)::before {
  transform: translateY(-50%) rotate(calc(45deg * 2));
}
@media (min-width: 640px) {
  .advantages__item {
    margin-bottom: 44px;
    font-size: 32px;
    line-height: 39px;
  }
  .advantages__item::before {
    width: 80px;
    height: 80px;
    left: -90px;
    background-size: 80px 80px;
  }
  .advantages__item:last-child {
    margin-bottom: 0;
  }
}
@media (min-width: 1920px) {
  .advantages__item {
    margin-bottom: 76px;
    font-size: 40px;
    line-height: 48px;
  }
  .advantages__item::before {
    width: 100px;
    height: 100px;
    left: -120px;
    background-size: 100px 100px;
  }
}
.button-base {
  display: block;
  max-width: 340px;
  padding: 15px;
