.enrollment-card {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  width: 320px;
  min-height: 575px;
  margin: 0 auto;
  padding-top: 58px;
  padding-bottom: 71px;
  padding-left: 10px;
  padding-right: 10px;
  background: linear-gradient(90deg, #1a21ca 0%, #3a66da 69.06%, #1a16d1 100%);
  box-sizing: border-box;
}
@media (min-width: 640px) {
  .enrollment-card {
    width: 640px;
    min-height: 589px;
    padding: 87px;
  }
}
@media (min-width: 1920px) {
  .enrollment-card {
    width: 1280px;
    min-height: 720px;
    padding: 120px;
    background-image: url("../img/circles_modal.svg"), url("../img/bg-elements_modal.svg"), linear-gradient(90deg, #1a21ca 0%, #3a66da 69.06%, #1a16d1 100%);
    background-size: 699px 673px, 1258px 631px, 100%;
    background-position: right 39px bottom 47px, right bottom, left top;
    background-repeat: no-repeat;
  }
}
.enrollment-card__toggle {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 24px;
  height: 24px;
  background-color: transparent;
}
@media (min-width: 640px) {
  .enrollment-card__toggle {
    top: 30px;
    right: 30px;
    width: 28px;
    height: 28px;
  }
}
@media (min-width: 1920px) {
  .enrollment-card__toggle {
    top: 42px;
    right: 92px;
    width: 75px;
    height: 75px;
  }
}
.enrollment-card__toggle::before,
.enrollment-card__toggle::after {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  border-radius: 3px;
}
@media (min-width: 1920px) {
  .enrollment-card__toggle::before,
  .enrollment-card__toggle::after {
